@font-face {
    font-family: title;
    src: url("./Fonts/titles.ttf");
}
  
  
@font-face {
    font-family: text;
    src: url("./Fonts/text.ttf");
}
  
  
@font-face {
    font-family: subtext;
    src: url("./Fonts/subtext.otf");
}
  
  h1 {
    font-family: title;
    color: white;
    font-size: 32px;
    margin: 0px;
  }
  
  p {
    font-family: text;
    color: white;
    margin-top: 20px;
    margin-bottom: 0px;
    font-size: 20px;
    #nav-imp {
      font-family: title;
      color: white;
      margin-top: 20px;
      margin-bottom: 0px;
      font-size: 20px;
    }
  }
  
  p1 {
    font-family: text;
    color: white;
    font-size: 15px;
    margin-bottom: 20px;
  }
  
  /*~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~*/
  
  div#search-bar {
    width: calc(100% - 82px);
    padding-left: 82px;
    z-index: 97;
    position: fixed;
    background-color: rgb(48, 48, 48);
    -webkit-app-region: drag;
    #minimize {
      margin: 16px 8px 12px 16px;
      float: right;
      box-sizing: border-box;
      -webkit-app-region: no-drag;
    }
    #maximize {
      margin: 16px 8px 12px 8px;
      float: right;
      box-sizing: border-box;
      -webkit-app-region: no-drag;
    }
    #close {
      margin: 16px 16px 12px 8px;
      float: right;
      box-sizing: border-box;
      -webkit-app-region: no-drag;
    }
    #spacer {
      float: right;
      height: 30px;
      width: 1px;
      background-color: #808080;
      margin: 9px 8px 9px 8px;
      opacity: 0.5;
    }
  }
  
  /*~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~*/
  
  
  #nav {
    height: 100%;
    width: 82px;
    background-color: rgb(34, 35, 38);
    z-index: 98;
    position: fixed;
    #main-page {
      margin: 25px 25px 21px 25px;
      float: left;
      box-sizing: border-box;
      -webkit-app-region: no-drag;
    }
    #browse {
      margin: 25px 25px 21px 25px;
      float: left;
      box-sizing: border-box;
      -webkit-app-region: no-drag;
    }
    #radio {
      margin: 25px 25px 21px 25px;
      float: left;
      box-sizing: border-box;
      -webkit-app-region: no-drag;
    }
    #library {
      margin: 25px 25px 21px 25px;
      float: left;
      box-sizing: border-box;
      -webkit-app-region: no-drag;
    }
    #oriz-spacer {
      width: 58px;
      height: 1px;
      background-color: #808080;
      margin: 0px;
      padding: 0px;
      margin-left: 12px;
      margin-right: 12px;
      float: left;
      box-sizing: border-box;
      -webkit-app-region: no-drag;
      opacity: 0.5;
    }
  }
  
  /*~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~*/
  
  #main {
    height: 100%;
    width: 100%;
    z-index: 2;
    position: fixed;
    overflow-y: scroll;
    padding-left: 82px;
    padding-right: 82px;
    #main-content {
      border: 0px;
      width: 100%;
      height: 100%;
      height: calc(100% - 48px);
    }
  }
  
  /*~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~*/
  
  body {
    z-index: 1;
    position: relative;
    margin: 0px;
    background-image: url(./Images/2.png);
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
  }
  
  html {
    z-index: 0;
    height: 100%;
    width: 100%;
  }